import React, { useEffect, useRef, useState } from 'react';
import Labelwithdescription from '../../Components/labelwithdescription/Labelwithdescription';
import { GET_MONITOR_COUNT, GET_MONITOR_VIEWS, UPDATE_CANVAS } from '../../API_Requests';
import mqtt from 'mqtt';
import {brokerUrl} from '../../API_Requests'
import {broker_topic} from '../../API_Requests'

export default function MonitoringStation() {
    const [monitorCount, setMonitorCount] = useState(0); 

    // Assuming count is a number

    useEffect(() => {
        // Initial fetch for monitor count
        GET_MONITOR_COUNT()
            .then(count => {
                setMonitorCount(count);
            })
            .catch(err => {
                console.error("Error fetching monitor count:", err);
            });

        let isMounted = true;


        // MQTT connection to Mosquitto
        const client = mqtt.connect(brokerUrl, {
            reconnectPeriod: 1000, // Reconnect every 1s if disconnected
        });



        client.on('connect', () => {
            console.log("Connected to Mosquitto broker");
            client.subscribe(broker_topic, (err) => {
                if (!err) {
                    console.log("Subscribed to cpgs");
                    GET_MONITOR_VIEWS() // Set the monitor count from the API

                } else {
                    console.error("Subscription error:", err);
                }
            });
        });

        client.on('message', (topic, message) => {
            // Handle incoming messages
               const data = JSON.parse(message.toString());
            UPDATE_CANVAS(data.data) // Update state with received data
        });

        client.on('error', (error) => {
            console.error("MQTT connection error:", error);
        });

        client.on('reconnect', () => {
            console.log("Reconnecting to Mosquitto...");
        });

        client.on('offline', () => {
            console.log("Mosquitto broker offline");
        });

        // Cleanup function
        return () => {
            isMounted = false; // Cleanup flag
            console.log("Component unmounted, polling stopped!");
        };
    }, []); // Empty dependency array: runs once on mount



    return (
        <>
            <div className='py-5'>
                <Labelwithdescription
                    label="Monitoring Station"
                    description="Currently watching spaces by this CPGS system (Maximum Capacity is 3 spaces)"
                />
                <p className='text-4xl font-bold'>{monitorCount}</p>
            </div>
            <hr className='my-5' />
            <div>
                <Labelwithdescription
                    label="Live Monitor"
                    description="Here you can monitor your spaces that are currently being monitored."
                />
            </div>
            <div className='flex' id='spaceViewContainer'>

            </div>

            <hr className='my-5' />
            <Labelwithdescription
                label="Detection Value"
                description="Once the Object Sensing Value crosses this line, the space is marked as occupied."
            />
            <p className='text-4xl font-bold'>50</p>
        </>
    );
}
