// SEND NEW SPACE COORDINATES TO THE SERVER THROUGHT SOCKET
export const GET_MONITOR_COUNT = async () => {
  let token = CheckUser()
  let spaceViewContainer = document.getElementById('spaceViewContainer')
  let data = {
    'task': 'GET_MONITOR_COUNT',
    'data': '',
    'token': token
  }

  let res = await axios.post(`${cpgsServerIp}/monitor_handler`, data, {
    headers: header
  })

  let NoOfMonitors = res.data.data
  for (let space = 0; space < NoOfMonitors; space++) {
    const monitor = document.getElementById(`liveMonitor${space}`)
    const liveLicensePlate = document.getElementById(`liveLicensePlate${space}`)
    // Create Monitors
    if (!monitor) {
      const div = document.createElement('div');
      div.className = 'flex-col';
      div.innerHTML = `
          <canvas id="liveMonitor${space}" alt="live Stream" class="border border-black rounded-lg m-2" ></canvas>
          <canvas id="liveLicensePlate${space}" alt="licensePlate" class="border border-black rounded-lg m-2" ></canvas>
      `;
      spaceViewContainer.appendChild(div);
    }
  }
}




// GET THE FRAMES OF MONITORING VIEWS
export const GET_MONITOR_VIEWS = () => {
  return new Promise(async (resolve, reject) => {
    try {
      let token = CheckUser();
      let data = {
        task: 'GET_MONITOR_VIEWS',
        data: '',
        token: token
      };
      let res = await axios.post(`${cpgsServerIp}/monitor_handler`, data, {
        headers: header
      });
    } catch (error) {
      reject(error);
    }
  });
};


export const UPDATE_CANVAS = (slotData) => {
  if (Array.isArray(slotData)) {
    slotData.forEach((slot, key) => {
      const monitorData = document.getElementById(`liveMonitor${key}`);
      const monitorLicensePlate = document.getElementById(`liveLicensePlate${key}`);

      if (monitorData && slot.spaceFrame) {
        // console.log(slot);
        const ctx = monitorData.getContext("2d");

        const img = new Image();
        img.onload = () => {
          ctx.clearRect(0, 0, monitorData.width, monitorData.height); // Clear previous frame
          ctx.drawImage(img, 0, 0, monitorData.width, monitorData.height);
        };
        img.onerror = () => {
          console.error(`Failed to load image for slot ${key}`);
        };
        img.src = slot.spaceFrame; // Ensure this is a valid base64 string or URL
      }

      // Update license plate image if available
      if (monitorLicensePlate && slot.licensePlate) {
        // console.log('license plate')
        const ctx = monitorLicensePlate.getContext("2d");
        const img = new Image();
        img.onload = () => {
          ctx.clearRect(0, 0, monitorLicensePlate.width, monitorLicensePlate.height); // Clear previous frame
          ctx.drawImage(img, 0, 0, monitorLicensePlate.width, monitorLicensePlate.height);
        };
        img.src = slot.licensePlate;
      }
    });
  } else {
    console.error("slotData is not an array:", slotData);
  }
};




